@import "../main.scss";

.handler {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Almoni Light";
  font-size: 16pt;
  right: 0;
  width: 200px;
  height: 40px;
  bottom: 40px;
  cursor: pointer;
  transition: background-color 0.2s ease-out, color 0.2s ease-out,
    border 0.2s ease-out;
  border-radius: 30px 0 0 30px;
  z-index: 5;
}

.active {
  color: black;
  background-color: $lightColor;
  border: 1px solid black;
}

.notActive {
  color: $lightColor;
  background-color: $darkColor;
  border: 1px solid rgba($lightColor, 0.5);
  border-right: 0;
  &:hover {
    background-color: rgba($lightColor, 0.5);
  }
}

@keyframes slideIn {
  from {
    right: -400px;
  }
}

@keyframes slideOut {
  to {
    right: -400px;
  }
}
