@import "../main.scss";

.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  background-color: $darkColor;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  mix-blend-mode: multiply;
  background: radial-gradient(rgb(200, 200, 200) 0%, $darkColor 85%);
  z-index: 1;
  opacity: 1;
}

.backgroundVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  object-fit: cover;
  opacity: 1;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}
