@import "../main.scss";

.wrapper {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 5px;
  bottom: 40px;
  display: flex;
  justify-content: space-between;
  direction: row-reverse;
  z-index: 5;
}

.arrowRight {
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 10px solid rgba($lightColor, 0.5);
  cursor: pointer;
  transition: border 0.2s ease-out;
  margin-left: 5px;
  &:hover {
    border-left: 10px solid $lightColor;
  }
}

.arrowLeft {
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-right: 10px solid rgba($lightColor, 0.5);
  cursor: pointer;
  transition: border 0.2s ease-out;
  margin-right: 5px;
  &:hover {
    border-right: 10px solid $lightColor;
  }
}
