@import "../main.scss";

.wrapper {
  position: absolute;
  width: 80vw;
  height: 80vh;
  top: 8vh;
  left: 50%;
  transform: translateX(-50%);
  opacity: 1;
  z-index: 5;
}

.header {
  position: absolute;
  left: 32%;
  font-size: 32pt;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  font-family: "Almoni Bold";
  color: white;
  opacity: 1;
  text-decoration: none;
  transition: color 0.2s ease;
}

.description {
  position: absolute;
  width: 28%;
  top: 60px;
  font-size: 14pt;
  line-height: 16pt;
  font-family: "Almoni Light";
  color: white;
  opacity: 1;
  white-space: pre-wrap;
}

.gallery {
  position: absolute;
  left: 0;
  height: 100%;
  width: 29%;
  z-index: 5;
  opacity: 1;
}

.demo {
  position: absolute;
  left: 32%;
  bottom: 0;
  height: 48vh;
  width: 58%;
  z-index: 5;
  opacity: 1;
}

.previewVideo {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  object-fit: cover;
  border: none;
}
.spotifyVideo {
  position: absolute;
  left: 0;
  top: 0;
  height: 29%;
  width: 100%;
  background-size: contain;
  object-fit: contain;
  border: none;
}

.link {
  position: absolute;
  opacity: 0;
  top: 8px;
  width: 30px;
  height: 30px;
  right: -40px;
  background-color: $lightColor;
  mask-repeat: no-repeat;
  mask-size: contain;
  mask-position: center;
  transition: opacity 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}
