@import "../main.scss";

.button {
  position: relative;
  height: 5px;
  width: 5px;
  border-radius: 5px;
  border: 1pt solid rgba($lightColor, 0.5);
  transition: background-color 0.2s ease;
  cursor: pointer;
  top: 1px;
}

.active {
  background-color: $lightColor;
}

.notActive {
  background-color: $darkColor;
  &:hover {
    background-color: rgba($lightColor, 0.5);
  }
}
